\"personalfileOpen\" for=\"groom-logo\">\n                        <img\n                            v-if=\"profilePicPreview\"\n                            :src=\"profilePicPreview\"\n                            alt=\"\"\n                        />\n                        <img\n                            v-else-if=\"sendProfile.imageOne\"\n                            :src=\"\n                                '/assets/grooms/uploads/' + sendProfile.imageOne\n                            \"\n                            alt=\"\"\n                        />\n                        <img\n                            v-else\n                            src=\"/assets/grooms/dashboard/ven-logo-upload.png\"\n                            alt=\"\"\n                        />\n                        <span>Add</span>\n                        <span v-if=\"errors && errors.img\" class=\"errMsg\">{{\n                            errors.img[0]\n                        }}</span>\n                    </label> -->\n                </div>\n            </div>\n            <div :class=\"[loader ? 'blur row about' : 'row about']\">\n                <div class=\"btn-container\">\n                    <button\n                        type=\"button\"\n                        class=\"tab-btn active\"\n                        data-id=\"company-details\"\n                        title=\"Personal Profile\"\n                        @click=\"\n                            toggleForm($event, true);\n                            personalfileOpen = false;\n                        \"\n                    ></button>\n                    <button\n                        type=\"button\"\n                        class=\"tab-btn\"\n                        data-id=\"groom-details\"\n                        title=\"Home Profile\"\n                        @click=\"\n                            toggleForm($event, true);\n                            personalfileOpen = true;\n                        \"\n                    ></button>\n                    <button\n                        type=\"button\"\n                        class=\"tab-btn\"\n                        data-id=\"office-details\"\n                        title=\"Office Profile\"\n                        @click=\"\n                            toggleForm($event, true);\n                            personalfileOpen = true;\n                        \"\n                    ></button>\n                </div>\n                <div class=\"about-content\">\n                    <form\n                        @submit.prevent=\"submitCProfile($event)\"\n                        class=\"content active\"\n                        id=\"company-details\"\n                    >\n                        <input\n                            type=\"file\"\n                            id=\"company-logo\"\n                            name=\"company-logo\"\n                            style=\"\n                                left: 42%;\n                                position: absolute;\n                                top: 13%;\n                                opacity: 0;\n                            \"\n                            @change=\"uploadImg($event, 'company')\"\n                        />\n                        <h1><span v-if=\"cedit\">Edit</span> Personal details</h1>\n                        <div style=\"display: flex; justify-content: flex-end\">\n                            <button\n                                v-if=\"cexist == 1 && cedit == false\"\n                                @click=\"editForm($event)\"\n                                style=\"\n                                    outline: none;\n                                    border: none;\n                                    margin-bottom: 10px;\n                                    margin-right: 20px;\n                                    padding: 0;\n                                \"\n                                type=\"button\"\n                            >\n                                <img\n                                    src=\"/assets/miscellenous/Frame-929.svg\"\n                                    alt=\"\"\n                                />\n                            </button>\n                            <a\n                                style=\"\n                                    margin-bottom: 10px;\n                                    margin-right: 20px;\n                                    text-decoration: none;\n                                \"\n                                :href=\"'/groom/' + routeSlug + '/Welcome'\"\n                                data-id=\"office-details\"\n                                type=\"button\"\n                            >\n                                <img\n                                    src=\"/assets/miscellenous/Frame-930.svg\"\n                                    alt=\"\"\n                                />\n                            </a>\n                        </div>\n                        <div class=\"groom-details-form-fields\">\n                            <div\n                                class=\"form-group\"\n                                :class=\"{\n                                    'form-group--error':\n                                        $v.groomProfile.name.$error,\n                                }\"\n                            >\n                                <label for=\"name\">\n                                    Name\n                                    <input\n                                        type=\"text\"\n                                        name=\"Name\"\n                                        id=\"Name\"\n                                        placeholder=\"Enter your name\"\n                                        v-model.trim=\"\n                                            $v.groomProfile.name.$model\n                                        \"\n                                    />\n                                    <span\n                                        v-if=\"errors && errors.name\"\n                                        class=\"errMsg\"\n                                        >{{ errors.name[0] }}</span\n                                    >\n                                    <vue-validation\n                                        :instance=\"$v\"\n                                        :nestedName=\"$v.groomProfile.name\"\n                                        :validation=\"[\n                                            'required',\n                                            'minLength',\n                                            'maxLength',\n                                            'alpha',\n                                        ]\"\n                                    ></vue-validation>\n                                </label>\n                            </div>\n                            <div\n                                class=\"form-group\"\n                                :class=\"{\n                                    'form-group--error':\n                                        $v.groomProfile.fatherName.$error,\n                                }\"\n                            >\n                                <label for=\"fatherName\">\n                                    Father Name\n                                    <input\n                                        type=\"text\"\n                                        name=\"fatherName\"\n                                        id=\"fatherName\"\n                                        placeholder=\"Enter your father name\"\n                                        v-model.trim=\"\n                                            $v.groomProfile.fatherName.$model\n                                        \"\n                                    />\n                                    <span\n                                        v-if=\"errors && errors.fatherName\"\n                                        class=\"errMsg\"\n                                        >{{ errors.fatherName[0] }}</span\n                                    >\n                                    <vue-validation\n                                        :instance=\"$v\"\n                                        :nestedName=\"$v.groomProfile.fatherName\"\n                                        :validation=\"[\n                                            'required',\n                                            'minLength',\n                                            'maxLength',\n                                            'alpha',\n                                        ]\"\n                                    ></vue-validation>\n                                </label>\n                            </div>\n                            <div\n                                class=\"form-group\"\n                                :class=\"{\n                                    'form-group--error':\n                                        $v.groomProfile.surname.$error,\n                                }\"\n                            >\n                                <label for=\"surname\">\n                                    Surname\n                                    <input\n                                        type=\"text\"\n                                        name=\"surname\"\n                                        id=\"surname\"\n                                        placeholder=\"Enter your surname\"\n                                        v-model.trim=\"\n                                            $v.groomProfile.surname.$model\n                                        \"\n                                    />\n                                    <span\n                                        v-if=\"errors && errors.surname\"\n                                        class=\"errMsg\"\n                                        >{{ errors.surname[0] }}</span\n                                    >\n                                    <vue-validation\n                                        :instance=\"$v\"\n                                        :nestedName=\"$v.groomProfile.surname\"\n                                        :validation=\"[\n                                            'required',\n                                            'minLength',\n                                            'alpha',\n                                        ]\"\n                                    ></vue-validation>\n                                </label>\n                            </div>\n                            <div\n                                class=\"form-group\"\n                                :class=\"{\n                                    'form-group--error':\n                                        $v.groomProfile.age.$error,\n                                }\"\n                            >\n                                <label for=\"age\">\n                                    Age\n                                    <input\n                                        type=\"text\"\n                                        name=\"age\"\n                                        id=\"age\"\n                                        placeholder=\"Enter your age\"\n                                        v-model.trim=\"\n                                            $v.groomProfile.age.$model\n                                        \"\n                                    />\n                                    <span\n                                        v-if=\"errors && errors.age\"\n                                        class=\"errMsg\"\n                                        >{{ errors.age[0] }}</span\n                                    >\n                                    <vue-validation\n                                        :instance=\"$v\"\n                       